import React, { Fragment, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';
import { withStyles } from '@material-ui/core';
import { Divider, ListItem, ListItemIcon, ListItemText, Tooltip } from '@material-ui/core';
import { ChevronUp, ChevronDown } from 'react-feather';
import Collapse from '@material-ui/core/Collapse';
import { useTranslation } from 'react-i18next';

import * as routes from '../../routes';

const useStyles = makeStyles(theme => ({
  backSub: {
    backgroundImage: `linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0) 100%),
                      linear-gradient(90deg, rgba(63, 81, 181, 1) 8px, rgba(0, 0, 0, 0.01) 8px)`,
    backgroundClip: 'content-box, padding-box',

  },
  linkStyle: {
    textDecoration: 'none',
    color: 'black',
  },
  iconStyle: {
    color: theme.palette.primary.main
  }
}))

export default function MenuItems (props){
  const { t } = useTranslation ();
  const classes = useStyles();
  const [state, setState] = useState({});

  function handleAction (type, event) {
    const val = !state[type];
    setState(prevState => ({
      ...prevState,
      [type]: val,
    }));
  };

  // Gera o menu baseado na lista da pasta Routes
  function generateListItem (item, linkStyle, iconStyle, backSub){  //Gera Item da lista com Link, styles
    return (
        <div key={item.title}>
        <Link className={linkStyle} to={item.path}>
          <ListItem 
            button 
            selected={window.location.href.indexOf(item.path) > -1}
            className={backSub} 
            style={{padding: "8px 16px"}}
          >
            <Tooltip title={t(item.title)} placement="right">
              <ListItemIcon className={iconStyle}>
                {item.icon}
              </ListItemIcon>
            </Tooltip>
            <ListItemText primary={t(item.title)} />
          </ListItem>
        </Link>
      </div>
    )
  }

    return (
      <Fragment>
        {routes.menu.map((item, index) => {
          if (item.divider) return <div key={index}><Divider /></div>;
          if (item.sub === undefined) //Se no tiver subitens
            return generateListItem(item, classes.linkStyle, classes.iconStyle, '');
          else { //Se tiver subitens (com collapse)
            return (
              <div key={item.title}>
                <ListItem button onClick={handleAction(item.title)}>
                  <Tooltip title={t(item.title)} placement="right">
                    <ListItemIcon>
                      {item.icon}
                    </ListItemIcon>
                  </Tooltip>
                  <ListItemText inset primary={t(item.title)} />
                    {state[item.title] ?
                    <ChevronUp /> :
                    <ChevronDown />}
                </ListItem>
                <Collapse in={state[item.title]} timeout="auto" unmountOnExit>

                  {item.sub.map((subitem,index) => {
                    return generateListItem(subitem, classes.linkStyle, classes.backSub);
                  })}

                </Collapse>
              </div>
            );
          }
        })}

      </Fragment>
    );

}
