import React, { Fragment } from 'react';
import { withStyles } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';

const styles = {
  floatmid: {
    position: 'absolute',
    zIndex: 999,
    margin: 'auto',
    top: 0,
    left: 0,
    bottom: 0,
    right: 0,
  },
  bgOpac: {
    backgroundColor: 'white',
    opacity: 0.6,
  }
};

function Loading(props) {
  const { classes } = props;
  return (
    <Fragment>
      <div className={[classes.floatmid, classes.bgOpac].join(' ')}></div>
      <Fragment>
        <CircularProgress className={classes.floatmid} /> 
      </Fragment>
    </Fragment>
  );
}

export default withStyles(styles)(Loading);