import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import {
  AppBar,
  Box,
  CssBaseline,
  Drawer,
  IconButton,
  Toolbar,
  Typography } from '@material-ui/core/';
import { ChevronLeft, Menu } from 'react-feather';
import MenuItems from '../navigation/MenuItems';
import { withRouter } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import SettingsToolbar from './SettingsToolbar';
import UserToolbar from './UserToolbar';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    backgroundColor: '#F0F0F0',
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1),
    marginLeft: 0,
    marginRight: theme.spacing(3),
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
    color: '#005baf',
    marginLeft: theme.spacing(1),
  },
  drawerPaper: {
    height: '100%',
    minHeight: '100vh',
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(0),
    [theme.breakpoints.up('md')]: {
      width: theme.spacing(7),
    },
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
  content: {
    flexGrow: 1,
    paddingTop: theme.spacing(8),
    padding: theme.spacing(3),
    display: 'block',
    height: 'auto',
    overflow: 'visible',
  },
  toolbar: {
    // paddingRight: 24, // keep right padding when drawer closed
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0px 0px',
    backgroundColor: 'white',
    ...theme.mixins.toolbar,
  },
  '@media print': {
    noprint: {
      display: 'none',
    },
    content: {
      flexGrow: 1,
      paddingTop: theme.spacing(4),
      padding: theme.spacing(3),
      display: 'block',
      height: 'auto',
      overflow: 'visible',
    },
  },
  typography: {
    padding: theme.spacing(2),
  },
}));

function NavigationDrawer(props) {
  const classes = useStyles();
  const { t } = useTranslation();

  const [open, setOpen] = React.useState(false);

  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };

  // const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
  if(!props.isAuthenticated) return (<> {props.children} </>)
  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        className={clsx(classes.appBar, open && classes.appBarShift, classes.noprint)}
      >
        <Toolbar variant="dense" className={classes.toolbar} style={{minHeight: 48}}>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
          >
            <Menu color="#005baf" />
          </IconButton>
          <Typography variant="h4" noWrap className={classes.title}>
            <Box fontFamily='Open Sans' fontWeight={600} fontSize={20}>
              {t('title')}
            </Box>
          </Typography>

          <SettingsToolbar />

          <UserToolbar logout={props.logout}/>

        </Toolbar>
      </AppBar>
      <Drawer
        variant="permanent"
        classes={{
          paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
        }}
        open={open}
      >
        <div className={classes.toolbarIcon} style={{minHeight: 0}}>
          <IconButton onClick={handleDrawerClose}>
            <ChevronLeft color="#005baf"/>
          </IconButton>
        </div><MenuItems />
      </Drawer>

      <main className={classes.content} >

          <div id="MainContent" >
            {props.children}
          </div>
          <Box pt={4}>
            {/* <Copyright /> */}
          </Box>
      </main>

    </div>
  );
}

export default withRouter(NavigationDrawer);
