import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Button, Grid, Paper, Typography } from '@material-ui/core';
import Loading from '../../components/loading/Loading';

import { templateNewPropolis0, templateNewPropolis1, templateNewPropolis2, templateNewPropolis3 } from './template';
import InputFacade from '../../components/facade/InputFacade';
import Accordion from '../../components/accordion/Accordion';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    '&:not(:last-child)': {
      marginBottom: theme.spacing(2),
    }
  },
  button: {
    height: '100%',
    minWidth: 85
  },
}))

export default function NewUser(props) {
  const { t } = useTranslation();
  const classes = useStyles()
  const [change, setChange] = useState(false)
  const [loading, setLoading] = useState(true)

  function handleSave() {
    // const validated = validateD0()
    // handleChange()

    // if(validated) {
    //     saveD0()
    //     .then(response => console.log(response))
    // }
  }

  function handleChange() {
      setChange(!change)
  }

  useEffect(() => {
    const handleMerge = async () => {
        // await mergeD0()

        if(isMounted) {
            setLoading(false)
        }
    }

    let isMounted = true

    if(!props.isNew) {
        handleMerge()
    } else {
        setLoading(false)
    }

    return function clean() {
        // avoid update state on unmount component
        isMounted = false
    }
  }, [props.isNew])

  return (
    <>
      <Grid container spacing={1}> 
        <Grid item xs={12}> 
          <Typography
          variant="h2"
          gutterBottom
          align="left">
          <Box  fontFamily='Open Sans' fontWeight={700}>
            {t('propolis.new')}
          </Box>
        </Typography>
        </Grid>

        <Grid item xs={12}>
          <Paper className={classes.root}>
            
            <Accordion
              title={t('propolis.d0')}
              panel="0d"
              onChange={handleChange}
              onSave={handleSave}
              isNew={false}
              first
            > 
               {loading ? (
                <Loading />
            ) : (
                <Grid container spacing={1}>
                    {templateNewPropolis0.map((field,index) => {
                        return (
                            <Grid item xs={field.sxs} key={'0dfield_'+index+(field.value)}>
                                <InputFacade
                                    id={"0df"+index}
                                    {...field}
                                    label={t(field.label)}
                                    onChange={value => { field.value = value }}
                                />
                            </Grid>
                        )
                    })}
                </Grid>
            )}
            </Accordion>

            <Accordion
              title={t('propolis.d1')}
              panel="1d"
              onChange={handleChange}
              onSave={handleSave}
              isNew={false}
            > 
               {loading ? (
                <Loading />
            ) : (
                <Grid container spacing={1}>
                    {templateNewPropolis1.map((field,index) => {
                        return (
                            <Grid item xs={field.sxs} key={'1dfield_'+index+(field.value)}>
                                <InputFacade
                                    id={"1df"+index}
                                    {...field}
                                    label={t(field.label)}
                                    onChange={value => { field.value = value }}
                                />
                            </Grid>
                        )
                    })}
                </Grid>
            )}
            </Accordion>


            <Accordion
              title={t('propolis.d2')}
              panel="2d"
              onChange={handleChange}
              onSave={handleSave}
              isNew={false}
            > 
               {loading ? (
                <Loading />
            ) : (
                <Grid container spacing={1}>
                    {templateNewPropolis2.map((field,index) => {
                        return (
                            <Grid item xs={field.sxs} key={'2dfield_'+index+(field.value)}>
                                <InputFacade
                                    id={"2df"+index}
                                    {...field}
                                    label={t(field.label)}
                                    onChange={value => { field.value = value }}
                                />
                            </Grid>
                        )
                    })}
                </Grid>
            )}
            </Accordion>


            <Accordion
              title={t('propolis.d3')}
              panel="3d"
              onChange={handleChange}
              onSave={handleSave}
              isNew={false}
              last
            > 
               {loading ? (
                <Loading />
            ) : (
                <Grid container spacing={1}>
                    {templateNewPropolis3.map((field,index) => {
                        return (
                            <Grid item xs={field.sxs} key={'3dfield_'+index+(field.value)}>
                                <InputFacade
                                    id={"3df"+index}
                                    {...field}
                                    label={t(field.label)}
                                    onChange={value => { field.value = value }}
                                />
                            </Grid>
                        )
                    })}
                </Grid>
            )}
            </Accordion>
            
            
          </Paper>

        </Grid>
      </Grid>
    </>
  )
}