import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Button, OutlinedInput, InputLabel, InputAdornment, FormControl } from '@material-ui/core';
import { Paper, Typography, Grow } from '@material-ui/core';
import { Lock, User } from 'react-feather';
import withStyles from '@material-ui/core/styles/withStyles';
import Particles from 'react-particles-js';
import Loading from '../../components/loading/Loading';

import AuthService from '../../services/AuthService';

import styles from '../../assets/jss/loginStyles';

class Login extends Component {

  constructor() {
    super();
    this.state = {
      username: '',
      password: '',
      error: false,
      errorMessage: '',
      token: null,
      loading: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.authService = new AuthService();
  }

  // handleSubmit = event => {
  //   event.preventDefault();
  //   //const { username, password } = this.state;
  //   //this.props.onSignIn(username, password);
  //   this.props.history.push(this.props.redirectPath);
  // };

  handleSubmit(event) {
    const { login } = this.props;
    event.preventDefault();
    console.log('handleSubmit')
    this.setState(prevState => ({
      ...prevState,
      loading: true,
    }));
    const data = new FormData(event.target);
    const username = data.get('username');
    const password = data.get('password');
    const result = this.authService.login(username, password);
    result.then(
      (resolve) => {
        console.log('resolve');
        console.log(resolve);
        if(resolve.error){
          this.handleErrorState('Wrong Username or Password')
        }
        else login(resolve); //Send to APP.jsx the token
      },
      (error) => {
        let message = 'Connection Problem - Please contact Quality Support Team';
        if (error.response) message = `${error.response.status}- ${error.response.data.error}`;
        if (error.response && error.response.status === 403) message = 'Wrong Username or Password';
        console.log(error.response);
        this.handleErrorState(message)
      },
    );

    this.setState(prevState =>({
      ...prevState,
      loginInfo: true,
    }));
  }

  handleErrorState = message => {
    this.setState(prevState => ({
      ...prevState,
      loading: false,
      error: true,
      errorMessage: message,
    }));
  }

  render() {
    const { classes } = this.props;
    const { errorMessage, error, loading } = this.state;

    return (
      <main className={classes.layout} id="main007">
        {/* animeted background */}
        <Particles
          params={{
            particles: {
              number: {
                value: 200,
              },
              size: {
                value: 4,
              },
            },
            interactivity: {
              events: {
                onhover: {
                  enable: true,
                  mode: 'repulse',
                },
              },
            },
          }}
          canvasClassName={classes.background}
        />
        {/* Login card */}
        <Grow in {...{ timeout: 500 }}>
          <Paper className={classes.paper} id="paper007" style={{"padding": "48px","borderRadius": "15px", "transform": "scale(1,1)"}} >
            <Typography variant="h4" align="center">
                Cadastro de Substncias da Prpolis
            </Typography>
            <Typography variant="subtitle1" align="center" style={{"marginTop": "10px"}}>
              Nome a decidir
            </Typography>

            <form className={classes.form} onSubmit={this.handleSubmit}>
              {/* Login input */}
              <FormControl margin="dense" variant="outlined" required fullWidth style={{ marginBottom: 8 }}>
                <InputLabel htmlFor="username">Login</InputLabel>
                <OutlinedInput
                  name="username"
                  autoComplete="username"
                  autoFocus
                  notched
                  labelWidth={50}
                  startAdornment={
                    <InputAdornment style={{"marginRight": "4px"}}>
                      <User color="#005baf" />
                    </InputAdornment>
                  }
                />
              </FormControl>

              {/* Password input*/}
              <FormControl margin="dense" variant="outlined" required fullWidth>
                <InputLabel htmlFor="password">Password</InputLabel>
                <OutlinedInput
                  name="password"
                  type="password"
                  autoComplete="current-password"
                  notched
                  labelWidth={82}
                  startAdornment={
                    <InputAdornment style={{"marginRight": "4px"}}>
                      <Lock color="#005baf"/>
                    </InputAdornment>
                  }
                />
              </FormControl>
              {/* Sign in Button */}
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
              >
                Sign in
                {loading && <Loading />}
              </Button>
            </form>
            
            {/* Error dialog */}
            {error && (
            <Typography variant="body2" gutterBottom align="center" color="error">
              {errorMessage}
            </Typography>)}

            <img
              src={process.env.PUBLIC_URL + '/images/logo.png'}
              style={{ height: '15px', marginTop: '30px' }}
              alt="Logo"
            />
          </Paper>
        </Grow>
      </main>
    );
  }
}

export default withRouter(withStyles(styles)(Login));
