import React, { Fragment } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Tooltip from '@material-ui/core/Tooltip';
import Box from '@material-ui/core/Box';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Button from '@material-ui/core/Button';
import { ChevronDown, Info } from 'react-feather';
import { useTranslation } from 'react-i18next';


const useStyles = makeStyles((theme) => ({
    infoIcon: {
        color: theme.palette.text.primary
    },
    chevIcon: {
        color: theme.palette.primary.main
    },
    alignCenter: {
        display: 'flex',
        alignItems: 'center'
    },
    expanded: {
        '&.Mui-expanded': {
            marginTop: 1,
        }
    }
}));

export default function Accordion(props) {
    const classes = useStyles();
    const { t } = useTranslation();
    const [expanded, setExpanded] = React.useState(props.expanded)

    const size = props.size ? parseInt(props.size) : 24;
    const iconConfig = {size: (2*size/3 + 'px') };

    function handleChange(e, isExpanded) {
        setExpanded(isExpanded)
        props.onChange && props.onChange(props.panel, isExpanded)
    }
    
    return (
        <Fragment>
            <ExpansionPanel
                expanded={props.expanded}
                defaultExpanded={props.first && props.isNew}
                classes={{ expanded: classes.expanded }}
                onChange={handleChange}
                panel={props.panel}
                TransitionProps={{ unmountOnExit: true, onEnter: props.onEnter, onExit: props.onExit }}
            >
                <ExpansionPanelSummary
                    expandIcon={<ChevronDown className={classes.chevIcon} />}
                    aria-controls="panel1bh-content"
                    id="panel1bh-header"
                    classes={{ content: classes.alignCenter }}
                >
                    <Box fontWeight={400} fontFamily="Open Sans" fontSize={18} color='system.darkGray'>
                        {props.title}
                    </Box>
                    {props.subTitle && (
                        <Box fontWeight={400} fontFamily="Open Sans" fontSize={18} color='system.darkGray'>
                            {props.subTitle}
                        </Box>
                    )}
                    {props.info && expanded && (
                        <Tooltip style={{ marginLeft: 10 }} title={<span dangerouslySetInnerHTML={{ __html: t(props.info) }} />} placement="right" arrow>
                            <Info {...iconConfig} className={classes.infoIcon} />
                        </Tooltip>
                    )}
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    {props.children}
                </ExpansionPanelDetails>
                {!props.noActions && (<ExpansionPanelActions>
                    <Button size="small" type="submit" color="secondary" onClick={props.onCancel}>{t('cancel')}</Button>
                    <Button size="small" variant={props.last ? "text" : "contained"} color="primary" onClick={props.onSave}>
                        {props.isNew ? (
                            t('create')
                        ) : (
                            t('save')
                        )}
                    </Button>
                    {props.last && (
                        <Button size="small" variant="contained" color="primary" onClick={props.onConclude}>
                            {t('concludeItem')}
                        </Button>
                    )}
                </ExpansionPanelActions>)}
            </ExpansionPanel>
        </Fragment>
    )
}
