import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next';
import { makeStyles } from '@material-ui/core/styles';
import { Box, Button, Grid, Paper, Typography } from '@material-ui/core';
import Loading from '../../components/loading/Loading';

import { templateNewUser } from './template';
import InputFacade from '../../components/facade/InputFacade';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
    '&:not(:last-child)': {
      marginBottom: theme.spacing(2),
    }
  },
  button: {
    height: '100%',
    minWidth: 85
  },
}))

export default function NewUser(props) {
  const { t } = useTranslation();
  const classes = useStyles()
  const [change, setChange] = useState(false)
  const [loading, setLoading] = useState(true)

  function handleSave() {
    // const validated = validateD0()
    // handleChange()

    // if(validated) {
    //     saveD0()
    //     .then(response => console.log(response))
    // }
  }

  function handleChange() {
      setChange(!change)
  }

  useEffect(() => {
    const handleMerge = async () => {
        // await mergeD0()

        if(isMounted) {
            setLoading(false)
        }
    }

    let isMounted = true

    if(!props.isNew) {
        handleMerge()
    } else {
        setLoading(false)
    }

    return function clean() {
        // avoid update state on unmount component
        isMounted = false
    }
  }, [props.isNew])

  return (
    <>
      <Grid container spacing={1}> 
        <Grid item xs={12}> 
          <Typography
          variant="h2"
          gutterBottom
          align="left">
          <Box  fontFamily='Open Sans' fontWeight={700}>
            {t('user.new')}
          </Box>
        </Typography>
        </Grid>

        <Grid item xs={8}>
          <Paper className={classes.root}>
           
            {loading ? (
                <Loading />
            ) : (
                <Grid container spacing={2}>
                    {templateNewUser.map((field,index) => {
                        return (
                            <Grid item xs={field.sxs} key={'newusr_'+index+(field.value)}>
                                <InputFacade
                                    id={"nu_field"+index}
                                    {...field}
                                    label={t(field.label)}
                                    onChange={value => { field.value = value }}
                                />
                            </Grid>
                        )
                    })}
                    <Grid item xs={12}>
                    <Button
                            size="small"
                            type="submit"
                            variant="contained"
                            color="primary"
                            className={classes.button}
                            onClick={() => console.log(`click`)}
                        >
                            {t('user.create')}
                        </Button>
                    </Grid>
                </Grid>
            )}
          
          </Paper>

        </Grid>
      </Grid>
    </>
  )
}