import React from 'react';
import { Route, Redirect } from 'react-router';

const PrivateRoute = ({
  component: Component,
  redirectTo,
  isAllowed,
  ...rest
}) => (
  <Route
    {...rest}
    render={props => {
      return isAllowed ? (
        <Component {...props} />
      ) : (
        <Redirect
          to={{
            pathname: redirectTo,
            state: {
              from: props.location,
            },
          }}
        />
      );
    }}
  />
);

export default PrivateRoute;
