import React, { Fragment } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import { 
  Avatar,
  Button,
  Menu,
  MenuItem,
} from '@material-ui/core/';
import { useTranslation } from 'react-i18next';
  
  
const useStyles = makeStyles((theme) => ({
  avatar: {
    marginRight: theme.spacing(1),
    width: theme.spacing(4),
    height: theme.spacing(4),
  },
}));
  
export default function UserToolbar (props){
  const classes = useStyles();
  const { t } = useTranslation();
  const [anchorUserEl, setAnchorUserEl] = React.useState(null);

  const handleUserOpen = (event) => {
    setAnchorUserEl(event.currentTarget);
  };
  const handleUserClose = () => {
    setAnchorUserEl(null);
  };
  
  return(
    <Fragment>
      <Button 
        variant="text"
        onClick={handleUserOpen}
        disableElevation
      >
        <Avatar 
          alt="User"
          src={process.env.PUBLIC_URL+'/images/user.jpg'}
          className={classes.avatar}
        />
      </Button>

      <Menu
        id="menu-appbar"
        anchorEl={anchorUserEl}
        anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        keepMounted
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}
        open={Boolean(anchorUserEl)}
        onClose={handleUserClose}
        style={{"marginTop": "30px"}}
      >
      <MenuItem disabled>Luiz Carlos Leite Loureno</MenuItem>
      <MenuItem onClick={handleUserClose}>{t('user.myaccount')}</MenuItem>
      <MenuItem onClick={() => props.logout()}>{t('user.logout')}</MenuItem>
      </Menu>
    </Fragment>
  );
}