import React, { Fragment, useEffect, useState } from 'react';
import {
  Switch,
  Route,
  Redirect,
  withRouter,
} from 'react-router-dom';
import { createMuiTheme, ThemeProvider } from '@material-ui/core';

import NavigationDrawer from './components/navigation/NavigationDrawer';
import * as routes from './routes';
import PrivateRoute from './routes/PrivateRoute';
import LoginPage from './views/login/Login';
import './App.css';

const theme = createMuiTheme({
  palette: {
    type: 'light',
    primary: {
      main: '#005baf',
    },
    system: {
      lightGray: '#F0F0F0',
      pink: '#F50057',
      darkGray: '#282C34'
    },
    status:{
      overdue: '#FFA117',
      open: '#40A5F6',
      onGoing: '#FFD60A',
      completed: '#5CB660',
      ineffective: '#F75E53',
      canceled: '#A7A7A0',
      filling:'#FFBC11',
      unavailable:'#8EA7B6',
    },
  },
  typography: {
    fontFamily: ['Roboto','Open Sans', 'sans-serif'].join(','),
    color: '#282C34',
    useNextVariants: true,
  },
})

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false);

  useEffect(() => {
    if(localStorage.getItem('tokenPSR')) setIsAuthenticated(true);
  },[])

  const setAuthTrue = () => {
    setIsAuthenticated(true);
    localStorage.setItem('tokenPSR', 'TOKEN VALUE');
  }
  const setAuthFalse = () => {
    setIsAuthenticated(false);
    localStorage.removeItem('tokenPSR');
  }
  const LoginProps = () => (isAuthenticated ? <Redirect to={{ pathname: "/home"}} /> : <LoginPage login={setAuthTrue}/>);
  let NotFound = props => ( <div> Not Found </div> );

  let showedContent = (
    <NavigationDrawer isAuthenticated={isAuthenticated} logout={setAuthFalse}>
        <Switch>
          {Object.keys(routes.privateRoutes).map(key => (
            <PrivateRoute
              redirectTo="/login"
              isAllowed={isAuthenticated}
              key={key}
              {...routes.privateRoutes[key]}
            />
          ))}
          <Route path="/login" component={LoginProps} /> {/* Redirect para pagina aps o login*/}
          <Route component={NotFound} />
        </Switch>
    </NavigationDrawer>
  );

  return (
    <Fragment>
      <ThemeProvider theme={theme}>
          <div id="appDiv">
              {showedContent}
          </div>
      </ThemeProvider>
    </Fragment>
  );
}

export default withRouter(App);
