import React from 'react';
//import Loadable from 'react-loadable';
import { PlusCircle, User, Home } from 'react-feather';
import Loading from '../components/loading/Loading';
import HomePage from '../views/home/Home';
import NewPropolis from '../views/propolis/NewPropolis';
import NewUser from '../views/user/NewUser'

// const LoadableHomePage = Loadable({
//   loader: () => import('../views/home/Home'),
//   loading: Loading,
// });

// Controle dos componentes e de roteamento
// Usa loadable, pagina continua Single Page App
// Mas s carrega, de maneira assincrona, quando o usurio faz a requisio
// Aqui lista a rota que o react-router cuida e o componente que  carregado separado
// Soluo para carregar mais rapido e continuar SPA
export const privateRoutes = {
  index: {
    path: '/',
    component: HomePage,
    exact: true,
  },
  home: {
    path: '/home',
    component: HomePage,
    exact: true,
  },
  newUser: {
    path: '/user/new',
    component: NewUser,
  },
  newPropolis: {
    path: '/propolis/new',
    component: NewPropolis,
  },
};

// Gerao do menu nesta varivel
// Baseado neste array ele gera o menu de maneira iterativa e sequencial
// Obrigatrio o uso de titulo, icone e do objeto com o componente e link
// Permite UM subnivel com a propriedade ~sub: []~ e dentro do array os mesmos objetos
export const menu = [
  {
    divider: true,
  }, {
    title: 'home.home',
    icon: <Home />,
    ...privateRoutes.index,
  }, {
    divider: true,
  }, {
      title:'propolis.new',
      icon: <PlusCircle />,
      ...privateRoutes.newPropolis,
  }, {
    divider: true,
  }, {
    title:'user.new',
    icon: <User />,
    ...privateRoutes.newUser,
  }, {
    divider: true,
  }
];
