import React, { Fragment } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import {
  Avatar,
  FormControl,
  IconButton,
  InputLabel,
  MenuItem,
  Popover,
  Select } from '@material-ui/core/';
import { useTranslation } from 'react-i18next';
import FlagBR from '../../lang/pt/BR.svg';
import FlagUS from '../../lang/en/US.svg';
import FlagDev from '../../lang/dev/dev.svg';

const flags = {
  'pt-BR': FlagBR,
  'en-US': FlagUS,
  "dev": FlagDev
}

const useStyles = makeStyles((theme) => ({
  formControl: {
    minWidth: '208px',
  },
  popOverBackground: {
    background: theme.palette.action.active
  },
  popOver:{
    padding: theme.spacing(2),
  },
  small: {
    width: theme.spacing(3),
    height: theme.spacing(3),
    marginRight: theme.spacing(1),
  },
  sameLine: {
    display: 'inline-flex',
  },
  iconColor: {
    color: theme.palette.primary.main,
    width: theme.spacing(3),
    height: theme.spacing(3),
  }
}));

export default function SettingsToolbar (){
  const classes = useStyles();
  const { t, i18n } = useTranslation();
  const [anchorPopEl, setAnchorPopEl] = React.useState(null);
  const lang = localStorage.getItem('i18nextLng')

  const handlePopOpen = (event) => {
    setAnchorPopEl(event.currentTarget);
  };
  const handlePopClose = () => {
    setAnchorPopEl(null);
  };

  const changeLanguage = lng => {
    i18n.changeLanguage(lng);
  };

  return(
    <Fragment>

      <IconButton color="inherit" onClick={handlePopOpen}>
        <Avatar
          alt="Flag"
          // src={lang === 'pt-BR' ? FlagBR : FlagUS}
          src={flags[lang]}
          className={classes.iconColor}
        />
      </IconButton>

      <Popover
        id={'Settings-popver'}
        open={Boolean(anchorPopEl)}
        anchorEl={anchorPopEl}
        onClose={handlePopClose}
        className={classes.popOverBackground}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
        <div className={classes.popOver}>
          <FormControl variant="outlined" className={classes.formControl}>
            <InputLabel id="language-select-label">
              {t('language')}
            </InputLabel>
            <Select
              labelId="language-select-label"
              id="language-select"
              value={i18n.language}
              label={t('language')}
            >
              <MenuItem
                value={"pt-BR"}
                onClick={() => changeLanguage('pt-BR')}
              >
                <div className={classes.sameLine}>
                  <Avatar
                    alt="Brazil Flag"
                    src={FlagBR}
                    className={classes.small}
                  />
                  Portugus - BR
                </div>
              </MenuItem>

              <MenuItem
                value={"en-US"}
                onClick={() => changeLanguage('en-US')}
              >
                <div className={classes.sameLine}>
                  <Avatar
                    alt="United States Flag"
                    src={FlagUS}
                    className={classes.small}
                  />
                  English - US
                </div>
              </MenuItem>

              {process.env.NODE_ENV === 'development' && 
                <MenuItem
                value={"dev"}
                onClick={() => changeLanguage('dev')}
                >
                <div className={classes.sameLine}>
                  <Avatar
                    alt="Robot"
                    src={FlagDev}
                    className={classes.small}
                  />
                  Programmer - DV
                </div>
              </MenuItem>
              }
            </Select>
          </FormControl>
        </div>
      </Popover>
    </Fragment>
    );
  }
