import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import { useTranslation } from 'react-i18next';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { DownloadCloud, Trash2, UploadCloud } from 'react-feather';

const useStyles = makeStyles(theme => ({
    color: {
        color: theme.palette.info.dark
    },
    input: {
        width: '100%',
        height: '100%',
        position: 'relative',
        background: theme.palette.grey[200],
        border: `1px dashed ${theme.palette.grey[500]}`,
        borderRadius: 4,
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        color: theme.palette.text.secondary,
        '&:hover': {
            border: `1px dashed ${theme.palette.info.dark}`,
            color: theme.palette.info.dark
        }
    },
    inputText: {
        width: '100%',
        height: '100%',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        textTransform: 'uppercase'
    },
    inputField: {
        position: 'absolute',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        opacity: 1,
        cursor: 'pointer',
        zIndex: '10'
    },
    deleteIcon: {
        color: theme.palette.grey[500]
    },
    downloadIcon: {
        marginRight: 10,
        color: theme.palette.primary.main
    },
    uploadIcon: {
        marginRight: 10,
    },
    deleteIconBox: {
        display: 'flex',
        justifyContent: 'flex-end',
        alignItems: 'center',
        paddingRight: 10
    },
    previews: {
        width: '100%',
        position: 'relative',
        marginBottom: theme.spacing(2)
    },
    file: {
        background: theme.palette.grey[200],
        border: `1px solid ${theme.palette.grey[400]}`,
        width: '100%',
        borderRadius: 4,
        marginLeft: 0,
        marginTop: 6,
        '&:hover': {
            background: theme.palette.grey[300],
            border: `1px solid ${theme.palette.grey[500]}`,
        }
    },
    button: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-start',
        padding: '6px 0px 6px 10px',
        textTransform: 'none',
        '&:hover': {
            background: 'transparent',
        }
    }
}))

export function AttachmentInput({ accept, onFiles, files, getFilesFromEvent }) {
    const classes = useStyles()
    const { t } = useTranslation();

    return (
        <div className={classes.input}>
            <div className={classes.inputText}>
                <UploadCloud size={30} className={classes.uploadIcon}/>
                <Typography variant="h5">
                    {t('upload')}
                </Typography>
            </div>
            <label className={classes.inputField}>
                <input
                    type="file"
                    accept={accept}
                    multiple
                    style={{display: 'none'}}
                    onChange={e => {
                        onFiles(getFilesFromEvent(e))
                    }}
                />
            </label>
        </div>
    )
}

export function AttachmentLayout({ dropzoneProps, files, input, previews }) {
    const classes = useStyles()

    return (
        <div>
            <div {...dropzoneProps}>
                {input}
            </div>
            <div className={classes.previews}>
                {previews}
            </div>
        </div>
    )
}

export function AttachmentPreview(props) {
    const { name } = props.meta
    const classes = useStyles()

    return (
        <Grid container spacing={0} className={classes.file}>
            <Grid item xs={11}>
                <Button fullWidth className={classes.button} onClick={e => alert(`Download ${name}`)}>
                    <DownloadCloud size="22px" className={classes.downloadIcon}/>
                    <Typography variant="body1">
                        {props.meta.name}
                    </Typography>
                </Button>
            </Grid>
            <Grid item xs={1} style={{display: 'flex', alignItems: 'center'}} className={classes.deleteIconBox}>
                <Trash2
                    size="20px"
                    className={classes.deleteIcon}
                    onClick={e => props.fileWithMeta.remove()}
                    style={{cursor: 'pointer'}}
                />
            </Grid>
        </Grid>
    )
}
