import React, { useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Box, FormControl, InputLabel, List, ListItem, ListItemAvatar, ListItemText, MenuItem, Select, TextField, Tooltip } from "@material-ui/core";
import { useTranslation } from "react-i18next";
import Dropzone from 'react-dropzone-uploader';
import { AttachmentPreview, AttachmentLayout, AttachmentInput } from './AttachmentFacade'
import moment from "moment";
import { User, Download, Info, Plus, Trash2 } from 'react-feather';

const useStyles = makeStyles((theme) => ({
  marginTop: {
    marginTop: theme.spacing(3),
  },
  issuerAvatarContent: {
    background: theme.palette.status.unavailable,
    borderRadius: '50%',
    minWidth: 0,
    width: 50,
    height: 50,
    marginRight: 10,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center'
  },
  issuerAvatar: {
    color: theme.palette.common.white
  },
  textStyle: {
    fontFamily: 'Roboto',
    fontSize: 14,
    fontWeight: 400,
    color: theme.palette.system.darkGray,
    '&.MuiInputLabel-root': {
      opacity: 0.7
    },
    '&.MuiInputLabel-shrink': {
      marginTop: 2,
    }
  },
}))

export default function InputFacade(props){
  const [value, setValue] = useState(props.value || null)
  const { t } = useTranslation();
  const classes = useStyles();

  let size = props.size ? parseInt(props.size) : 24;
  let iconConfig = { size: (2 * size / 3 + 'px') };
  let circleSize = size + 'px';

  const getClasses = (names) => { // Merge styles
    return names ? names.map(name => classes[name]).join(' ') : ''
  }

  function handleChange(e) {
    setValue(e.target.value)
    props.onChange && props.onChange(e.target.value)
  }

  const handleChangeStatus = ({ meta, file }, status, allFiles) => {
    const files = allFiles
      .filter(item => {
        return !(status === 'removed' && meta.id === item.meta.id)
      })
      .map(item => item.file)
    const e = {
      target: {
        value: files
      }
    }

    handleChange(e)

    // console.log(value.target.value, allFiles)
    // props.onChange && props.onChange(value)
  }
  const handleSubmit = (files, allFiles) => {
    // console.log(files.map(f => f.meta))
    allFiles.forEach(f => f.remove())
  }
  let component;

  switch(props.comptype){
    case 'text':
      component = (
        <TextField
          {...props}
          fullWidth
          autoComplete="false"
          variant="outlined"
          helperText={props.helperText ? t(props.helperText) : false}
          multiline={props.multiline}
          rows={props.multiline ? props.rows : 1}
          className={getClasses(props.styles)}
          InputLabelProps={{
            className: classes.textStyle,
          }}
          InputProps={{
            className: classes.textStyle,
          }}
          value={value || ''}
          // inputProps={{
          //   maxLength: null
          // }}
          onChange={handleChange}
          error={props.error}
        />
      );
    break;
    case 'select':
      component = (
        <FormControl
          required={props.required || false}
          disabled={props.disabled}
          fullWidth variant="outlined"
          error={props.error || false}>
          <InputLabel className={classes.textStyle}>{props.label}</InputLabel>
          <Select
            id={props.id}
            value={value || ''}
            onChange={handleChange}
            label={props.label}
            inputProps={{
              className: classes.textStyle
            }}
          >
            {props.options.map((opt, index) =>
              (<MenuItem key={index} value={opt.value}>{t(opt.label)}</MenuItem>)
            )}
          </Select>
        </FormControl>
      );
    break;
    case 'person':
      component = (
        <>
          <List style={{ margin: "-8px", paddingTop: 0, paddingBottom: 0 }}>
            <ListItem>
              <ListItemAvatar className={classes.issuerAvatarContent}>
                <User className={classes.issuerAvatar} size="32px" />
              </ListItemAvatar>
              <ListItemText
                primary={value || t(props.label)}
                primaryTypographyProps={{ className: classes.textStyle }}
                secondary={moment(props.date).format('DD/MM/YYYY - HH:mm')}
                secondaryTypographyProps={{ className: classes.textStyle, style: { opacity: 0.7 } }}
              />
            </ListItem>
          </List>
        </>
      );
      break;
    case 'static':
      component = (
        <>
          <Box
            fontFamily={props.isTitle ? 'Open Sans' : 'Roboto'}
            fontSize={props.isTitle ? 18 : 14}
            fontWeight={400}
            colro='system.darkGray'
            lastitem={props.lastitem && props.lastitem.toString()}
            className={getClasses(props.styles)}
            style={{ display: 'flex', alignItems: 'center' }}
          >
            <span dangerouslySetInnerHTML={{ __html: t(props.label) }} />{props.required && <span className={classes.asterisk}> *</span>}
            {props.info && (
              <Tooltip style={{ marginLeft: 10 }} title={<span dangerouslySetInnerHTML={{ __html: t(props.info) }} />} placement="right" arrow>
                <Info {...iconConfig} color={'#000'} />
              </Tooltip>
            )}
          </Box>
        </>
      );
      break;
    case 'horizontal':
      component = (<><hr className={getClasses(props.styles)} /></>);
      break;
    case 'attachment':
      component = (<Dropzone
        onChangeStatus={handleChangeStatus}
        onSubmit={handleSubmit}
        PreviewComponent={AttachmentPreview}
        LayoutComponent={AttachmentLayout}
        InputComponent={AttachmentInput}
        initialFiles={props.files || []}
        addClassNames={{ dropzone: getClasses(props.styles) }}
        styles={{ dropzone: { minHeight: 165, height: 165, maxHeight: 165, overflow: 'hidden' } }}
      />);
      break;
    default:
      console.log("Component not found")
      console.log(props.comptype)
  }
  return (
    <> {component} </>
  );
}